[data-tabs] {
    // overflow: hidden
    [data-tabs-control] {
        cursor: pointer;
        user-select: none;
        &[disabled],
        &.is-disabled {
            cursor: not-allowed;
            @include nosel; } }
    [data-tabs-wrapper] {
        position: relative; }
    [data-tabs-container] {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s, visibility 0.3s;
        &.is-open {
            position: relative;
            opacity: 1;
            visibility: visible; } } }

.tabs {
    width: 100%;
    display: flex;
    justify-content: space-between;
    &__out {
        width: 50%; }
    &__wrapper {
        width: 50%; }
    &__container {
        padding: 4px 0 0 140px; }
    &__list {
        counter-reset: number; }
    &__subitem {
        position: relative;
        padding-left: 31px;
        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: $red; }
        &:not(:first-child) {
            margin-top: 20px; } } }


.menu-item {
    position: relative;
    padding: 0 40px 0 60px;
    font-family: $sofia;
    font-size: 28px;
    color: $greyMid;
    transition: color .3s;
    &:before {
        counter-increment: number;
        content: "0"counter(number);
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        font-size: 14px; }
    &:after {
        content: "";
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 40px;
        height: 1px;
        background-color: $red;
        transition: opacity .3s, visibility .3s; }
    &:not(:first-child) {
        margin-top: 10px; }
    &.is-active {
        color: $red;
        &:after {
            opacity: 1;
            visibility: visible; } } }
