body {
  background: #FFF;
  line-height: 1.4;
  font-family: ProximaNova, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.667;
  color: #817D8E;
}

.section-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(152, 149, 162, 0.1);
}

.inner,
.inner-lg,
.inner-lg-pl {
  max-width: 1340px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

@media only screen and (max-width: 1339px) {
  .inner,
  .inner-lg,
  .inner-lg-pl {
    padding: 0 60px;
  }
}

.inner-lg-pl {
  padding-left: 148px;
}

@media only screen and (max-width: 1339px) {
  .inner-lg-pl {
    padding-left: 10.9385%;
  }
}

@media only screen and (max-width: 1023px) {
  .inner-lg-pl {
    padding: 0 30px;
  }
}

.inner {
  padding: 0 150px;
}

@media only screen and (max-width: 1339px) {
  .inner {
    padding: 0 10.9385%;
  }
}

@media only screen and (max-width: 1023px) {
  .inner {
    padding: 0 30px;
  }
}

.title-lg {
  font-family: SofiaPro, sans-serif;
  font-size: 200px;
  line-height: 1;
  font-weight: 900;
  color: #817D8E;
}

.title-lg_light {
  color: rgba(129, 125, 142, 0.1);
}

@media only screen and (max-width: 1023px) {
  .title-lg {
    font-size: 140px;
  }
}

@media only screen and (max-width: 599px) {
  .title-lg {
    font-size: 60px;
  }
}

.title, .works__filter-btn {
  font-family: SofiaPro, sans-serif;
  font-size: 52px;
  line-height: 1.15;
  font-weight: 400;
  line-height: 1;
  color: #3E3852;
}

@media only screen and (max-width: 599px) {
  .title, .works__filter-btn {
    font-size: 28px;
  }
}

.title-sm {
  font-family: SofiaPro, sans-serif;
  font-size: 28px;
  line-height: 1.428;
  color: #3E3852;
}

@media only screen and (max-width: 599px) {
  .title-sm {
    font-size: 18px;
    line-height: 1.5;
  }
}

.text-light {
  color: #9F9CA9;
}

.block {
  display: flex;
}

.main-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #fff;
  transition: opacity 1s, visibility 1s;
  text-align: center;
  font-size: 0;
}

.main-loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.main-loader.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.main-loader__text {
  position: relative;
  line-height: 1;
  font-size: 200px;
  color: #fff;
  font-family: SofiaPro, sans-serif;
  vertical-align: middle;
  font-weight: 900;
  text-shadow: 0px 20px 69px rgba(78, 73, 122, 0.11);
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
  display: inline-block;
}

body.is-loaded .main-loader__text {
  opacity: 1;
  visibility: visible;
  transition-delay: 1s;
}

.works {
  margin: 227px auto;
  overflow: hidden;
}

.works__title {
  position: relative;
}

.works__title_filter span {
  display: inline-block !important;
}

.works__title_filter:after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 6px 5px;
  border-color: transparent transparent #F03454 transparent;
  transition: opacity 0.8s 1s;
  opacity: 0;
}

.is-animate .works__title_filter:after {
  opacity: 1;
}

.works__container {
  position: relative;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.works__container.is-init {
  opacity: 1;
  visibility: visible;
}

.works__item {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1023px) {
  .works__item {
    width: calc(50% - 50px);
  }
}

.works__item_padding {
  position: absolute;
  right: 0 !important;
  top: 0 !important;
  left: auto !important;
  height: 200px;
}

.works__filter-btn {
  background-color: transparent;
}

.work {
  padding-bottom: 110px;
}

.work__image-wrap {
  overflow: hidden;
  transition: transform 0.3s;
  background-color: #ccc;
  backface-visibility: hidden;
}

.no-touch .work__image-wrap:hover {
  transform: scale(0.97);
}

.no-touch .work__image-wrap:hover .work__image_scale {
  transform: scale(1.1);
}

.work__image {
  transition: transform 0.3s;
}

.work__image img {
  max-width: 100%;
  display: block;
}

.js-masonry-width {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1023px) {
  .js-masonry-width {
    width: calc(50% - 50px);
  }
}

.js-masonry-gutter {
  width: 120px;
}

@media only screen and (max-width: 1023px) {
  .js-masonry-gutter {
    width: 100px;
  }
}

.filter-controls {
  display: flex;
  align-items: center;
}

.filter-controls__item {
  flex: 0 0 20%;
  min-width: 20%;
}

[data-anim-from] {
  position: relative;
}

[data-anim-from].is-animate {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

[data-anim-text-parent] {
  overflow: hidden;
  position: relative;
  display: block;
}

[data-anim="text-from-bottom"] {
  display: inline-block;
  position: relative;
  transform: translateY(102%);
}

[data-rows] [data-anim="text-from-bottom"] {
  transition: inherit;
}

[data-anim="from-bottom"] {
  transition: opacity 1s, visibility 1s, transform 1s;
  transform: translateY(20%);
  opacity: 0;
  visibility: hidden;
}

[data-rows] {
  visibility: hidden;
}

[data-rows].is-animate,
[data-scroll-trigger].is-animate [data-rows] {
  visibility: visible;
}

[data-anim="from-top"] {
  position: relative;
  transform: translateY(-100%);
  opacity: 0;
}

.services__content {
  position: relative;
  padding: 265px 0 320px;
}

.services__bg {
  width: 50%;
}

.services__title {
  margin-bottom: 35px;
}

.services__header {
  max-width: 400px;
}

.services__tabs {
  margin-top: 110px;
}

[data-tabs] [data-tabs-control] {
  cursor: pointer;
  user-select: none;
}

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled {
  cursor: not-allowed;
  user-select: none;
}

[data-tabs] [data-tabs-wrapper] {
  position: relative;
}

[data-tabs] [data-tabs-container] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

[data-tabs] [data-tabs-container].is-open {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.tabs {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.tabs__out {
  width: 50%;
}

.tabs__wrapper {
  width: 50%;
}

.tabs__container {
  padding: 4px 0 0 140px;
}

.tabs__list {
  counter-reset: number;
}

.tabs__subitem {
  position: relative;
  padding-left: 31px;
}

.tabs__subitem:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #F03454;
}

.tabs__subitem:not(:first-child) {
  margin-top: 20px;
}

.menu-item {
  position: relative;
  padding: 0 40px 0 60px;
  font-family: SofiaPro, sans-serif;
  font-size: 28px;
  color: #9F9CA9;
  transition: color .3s;
}

.menu-item:before {
  counter-increment: number;
  content: "0" counter(number);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 14px;
}

.menu-item:after {
  content: "";
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 40px;
  height: 1px;
  background-color: #F03454;
  transition: opacity .3s, visibility .3s;
}

.menu-item:not(:first-child) {
  margin-top: 10px;
}

.menu-item.is-active {
  color: #F03454;
}

.menu-item.is-active:after {
  opacity: 1;
  visibility: visible;
}

.filter-btn {
  background-color: transparent;
  font-family: ProximaNova, sans-serif;
  font-size: 18px;
  line-height: 1.667;
  color: #9F9CA9;
}

.filter-btn.is-active {
  color: #F03454;
}
